body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eeeeee;
  min-height: 100vh;
}
a, li{
  list-style: none;
  text-decoration: none;
}
::-webkit-scrollbar{
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  width: 100vw;
  height: auto;
}
.curseur-perso{
  width: 30px;
  height: 30px;
  background-color: #f1d00a;
  border-radius: 50%;
  position: absolute;
  pointer-events: none;
  z-index: 2;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media screen and (max-width: 1024px) {
  .curseur-perso{
    display: none;
  }
}

.aboutme {
  display: flex;
  padding-top: 100px;
}
.left {
  width: 50%;
  padding-top: 50px;
}
.left h1 {
  padding-left: 25%;
  margin: 0;
  font-size: 96px;
  font-family: "Special Elite", cursive;
  z-index: 1;
  position: relative;
}
.right {
  width: 50%;
  padding-top: 5%;
  font-family: "Special Elite", cursive;
  font-size: 24px;
}
.right p {
  margin-left: 10%;
  margin-right: 25%;
}
.right span {
  color: #f1d00a;
}
.right span a {
  color: #f1d00a;
  font-weight: bold;
}
.right img {
  width: 50%;
  margin-left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin-top: -10%;
  border-radius: 5%;
}
.aboutRound {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #f1d00a;
  position: absolute;
  top: 300px;
  left: 25%;
  -webkit-animation: scale 1.5s;
          animation: scale 1.5s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
@-webkit-keyframes scale{
  0%{
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100%{
    -webkit-transform: scale(10);
            transform: scale(10);
  }
}
@keyframes scale{
  0%{
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100%{
    -webkit-transform: scale(10);
            transform: scale(10);
  }
}
.aboutText {
  width: 80%;
  margin: 0 auto;
  margin-top: 120px;
  margin-bottom: 120px;
}
.aboutText p {
  font-size: 28px;
  font-family: "Special Elite", cursive;
}
.aboutText span {
  color: #f1d00a;
  font-weight: bold;
}
@media screen and (max-width: 1500px) {
  .aboutme {
    display: flex;
    flex-direction: column;
  }
  .left {
    width: 100%;
  }
  .left h1 {
    padding-left: 12%;
  }
  .right {
    margin-left: 40%;
  }
}
@media screen and (max-width: 1200px) {
  .left h1 {
    padding-left: 12%;
    font-size: 70px;
  }
  @-webkit-keyframes scale{
    0%{
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100%{
      -webkit-transform: scale(7);
              transform: scale(7);
    }
  }
  @keyframes scale{
    0%{
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100%{
      -webkit-transform: scale(7);
              transform: scale(7);
    }
  }
  

}
@media screen and (max-width: 1024px) {
  .right {
    width: 100%;
    margin-left: 10%;
  }
  .aboutText p {
    font-size: 20px;
  }
  
}
@media screen and (max-width: 675px) {
  .right {
    width: 90%;
    margin-left: 5%;
    font-size: 18px;
  }
  .left h1 {
    padding-left: 12%;
    font-size: 40px;
  }
  .aboutRound{
    top: 250px;
    left: 20%;
  }
  @-webkit-keyframes scale{
    0%{
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100%{
      -webkit-transform: scale(4);
              transform: scale(4);
    }
  }
  @keyframes scale{
    0%{
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100%{
      -webkit-transform: scale(4);
              transform: scale(4);
    }
  }


}


.info{
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-left: 10%;
    font-family: "Special Elite", cursive;

}
form{
    margin-top: 20px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

}
.infoSend{
    width: 45%;
}
label{
    font-size: 22px;
    display: flex;
    flex-direction: column;
    width: 45%;
    margin-top: 20px;
}
input{
    height: 30px;
    width: 100%;
    border-radius: 8px;
    background-color: #eee;
    padding: 10px;
    font-size: 16px;
    border: 1px solid black;
}
.message{
    width: 100%;
}
.textarea{
    width: 100%;
}
textarea{
    border-radius: 8px;
    background-color: #eee;
    height: 200px;
    border: 1px solid black;
    padding: 10px;
    font-size: 16px;
    width: 100%;
    margin-bottom: 30px;
}
.button{
    color: #eee;
    background-color: black;
    cursor: pointer;
    line-height: 0px;
    width: 20% !important;
    margin-left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin-bottom: 30px;

}

@media screen and (max-width: 800px) {
    .info{
        width: 80%;
    
    }
}
@media screen and (max-width: 675px){
    form{
        flex-direction: column;
    }
    .infoSend{
        width: 100%;
    }
    .button{
        width: 40% !important;
    }
    
}

.divider{
    width: 50%;
    height: 3px;
    background: #000;
    margin-top: 100px;
    margin-left: 13%;
}
.mobilNav{
    display: none;
}
nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 5%;
    padding-right: 5%;
    height: 100px;
    font-family: 'Special Elite', cursive;

}
.logo{
    width: 75px;
    height: 75px;
    background: #000;
    border: 3px solid #f1d00a;
    border-radius: 50%;
    display: flex;
    align-items: flex-end;
}
.logo:hover{
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
    transition: 1s;
}
.logo p{
    color: #fff;
    font-size: 36px;
    font-weight: bold;
    margin-bottom: 5px;
    margin-left: 7px;
}
.logo span{
    color: #f1d00a;
}
.imgLogo{
    width: 100%;
    height: 100%;
}
.items{
    display: flex;
}
.items a{
    font-size: 24px;
    color: black;
    margin-left: 25px;
}
.items a:hover{
    font-weight: bold;
}
.active_nav a{
    border-bottom: 4px solid #f1d00a;
}

@media screen and (max-width: 769px){
    nav{
        padding-right: 0%;
    }
    .mobilNav{
        display: block;
        z-index: 1;
        background: none;
        border: none;
        cursor: pointer;
        position: absolute;
        right: 5%;
        top: 25px;
    }
    .mobilNav img{
        width: 40px;
    }
    .navItems{
        display: none;
        background-color: #fff;
       background: #000;
    }
    .active{
        display: block;
        height: 100vh;
        position: absolute;
        top: 0;
        right: 0;
    }
    .active_nav a{
        border-bottom: 3px solid #f1d00a;
    }
    .active img{
        color: #fff;
    }
    .items{
        flex-direction: column;
        padding-top: 100px;
        padding-right: 35px;
        text-align: right;
    }
    .items li{
        margin-bottom: 15px;
        color: #fff;
    }
    .items a {
        color: #fff;
    }
}
h2{
    margin-left: 13%;
    margin-top: 50px;
    font-size: 48px;
    font-family: 'Special Elite', cursive;
}
h2 span{
    color: #f1d00a;
}
.carousel{
    width: 80%;
    margin: 0 auto;
    margin-top: 100px;
}
.alice-carousel__stage-item{
    cursor: pointer;
}
.alice-carousel__stage-item img{
    width: 90%;
    height: 400px;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
}
.alice-carousel__stage-item img:hover{
    -webkit-filter: grayscale(0%);
            filter: grayscale(0%);
    transition: 0.4s;
}
.alice-carousel__dots-item.__active{
    background-color: #f1d00a !important;
}

@media screen and (max-width: 675px){
    h2{
        font-size: 32px;
    }
    .alice-carousel__stage-item img{
        height: 300px;
    }
    
    
}


.footer{
    min-height: 250px;
    background-color: black;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 25px;
    padding-left: 10%;
    padding-right: 10%;
}
.round{
    width: 75px;
    height: 75px;
    background: black;
    border-radius: 50%;
    border: #f1d00a 3px solid;
    align-items: flex-end;
}
.round:hover{
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
    transition: 1s;
}

.footerLogo p{
    color: #fff;
    font-size: 36px;
    font-weight: bold;
    padding-top: 30px;
    margin-top: 5px;
    margin-left: 7px;
    font-family: 'Special Elite', cursive;
}
.footerLogo h2{
    color: #eee;
    font-size: 26px;
    margin: 0;
    margin-top: 15px;
}
.footerLogo span{
    color: #f1d00a;
}
.footerMenu li{
    margin-bottom: 10px;

}
.footerMenu a{
    color: #eee;
    font-family: 'Special Elite', cursive;
    font-size: 20px;
}
.footerMenu a:hover{
    font-weight: bold;
}
.footerContact p, a{
    color: #eee;
    font-family: 'Special Elite', cursive;
    font-size: 20px;
}
.footerContact a:hover{
    font-weight: bold;
}
@media screen and (max-width: 800px) {
    .footer{
        flex-direction: column;
    }
    .footerItems{
        padding: 0;
    }
    

}
.container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 200px;
    margin-bottom: 200px;
}
.vignette{
    width: 30%;
    height: 400px;
    margin-bottom: 50px;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
}
.vignette img{
    width: 100%;
    height: 300px;
}
.vignette:hover{
    -webkit-filter: grayscale(0%);
            filter: grayscale(0%);
    transition: 0.4s;
    color: #f1d00a;
}
.vignette h2{
    font-size: 30px;
}

@media screen and (max-width: 1024px) {
    .vignette{
        width: 45%;
    }
    
}
@media screen and (max-width: 675px) {
    .vignette{
        width: 70%;
    }
    
}
